import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,} from 'ionic-angular';
import { RestProvider } from '../../../providers/rest/rest';
import 'rxjs/add/operator/map'



@IonicPage()
@Component({
  selector: 'page-pesquisacurso',
  templateUrl: 'pesquisacurso.html',
})
export class PesquisacursoPage {
    cursos: any;
    searchTerm: string = '';
    items: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public restProvider: RestProvider) {
      this.getCursos();   
  }
  
  getCursos() {
    this.restProvider.getRest('{"operacao":"getcursos"}')
    .then(data => {
      console.log(data);
    this.cursos = data;
        });
    }
    
    filterItems(searchTerm){

        return this.cursos.filter((item) => {
            return item.AD0013TurmaNome.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
        });
    } 
    setFilteredItems() {

        this.items = this.filterItems(this.searchTerm);

    }
    goToSingleCurso(i){        
      console.log(this.cursos[i].FK0011ProgramacaoCurso);
      console.log(this.cursos[i]);
      this.navCtrl.push('SinglecursoPage', {singleCursos:this.cursos[i]})
    }
    


}



